import React from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Get_Api from './Get_Api1';
import Paper from '@material-ui/core/Paper';
function TabPanel(props) {
  const { children, value, index} = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      
    >
      <Box p={7}>{children}</Box>
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: '100%',
  },
}));

export default function FullWidthTabs() {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = index => {
    setValue(index);
  };

  return (

    <Paper className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab label="Models" {...a11yProps(0)} />
          <Tab label="Marques" {...a11yProps(1)} />
          <Tab label="Etats" {...a11yProps(2)} />
          <Tab label="Categories" {...a11yProps(3)} />
          <Tab label="Assurances" {...a11yProps(4)} />
          <Tab label="Vidanges" {...a11yProps(5)} />
          <Tab label="Couleurs" {...a11yProps(6)} />
          <Tab label="Missions" {...a11yProps(7)} />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabPanel value={value} index={0} dir={theme.direction}>
        <Get_Api name='Models'/>
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
        <Get_Api name='Marques'/>
        </TabPanel>
        <TabPanel value={value} index={2} dir={theme.direction}>
          <Get_Api name='Etats'/>
        </TabPanel>
        <TabPanel value={value} index={3} dir={theme.direction}>
        <Get_Api name='Categories'/>
        </TabPanel>
        <TabPanel value={value} index={4} dir={theme.direction}>
        <Get_Api name='Etats'/>
        </TabPanel>
        <TabPanel value={value} index={5} dir={theme.direction}>
          Item four
        </TabPanel>
        <TabPanel value={value} index={6} dir={theme.direction}>
          Item four
        </TabPanel>
        <TabPanel value={value} index={7} dir={theme.direction}>
          Item four
        </TabPanel>
      </SwipeableViews>
      </Paper>
  );
}
