import React,{Component} from 'react';
class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      designationAr: '',
      designationFr:''
    };

    this.handleChangedesignationFr = this.handleChangedesignationFr.bind(this);
    this.handleChangedesignationAr = this.handleChangedesignationAr.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChangedesignationFr(event) {
    this.setState({designationFr: event.target.value});
  }
  handleChangedesignationAr(event) {
    this.setState({designationAr: event.target.value});
  }

  handleSubmit(event) {
    event.preventDefault();
    fetch('http://127.0.0.1:8080/Etats', {
      method: 'POST',
      body: JSON.stringify({
        'designationFr':this.state.designationFr,
        'designationAr':this.state.designationAr
      }),
      headers: {
        "Content-Type": "application/json"
      },
    });
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label htmlFor="designationFr">Des_Fr</label>
        <input id="designationFr" name="designationFr" type="text" value={this.state.designationFr} onChange={this.handleChangedesignationFr}/>

        <label htmlFor="designationAr">Des_Ar</label>
        <input id="designationAr" name="designationAr" type="text" value={this.state.designationAr} onChange={this.handleChangedesignationAr} />

        <button>Send data!</button>
      </form>
    );
  }
}
export default MyForm;
