import React from 'react';
import clsx from 'clsx';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import { makeStyles, useTheme,fade } from '@material-ui/core/styles';
import {Menu,IconButton,InputBase,MenuItem,ListItem,ListItemText,ListItemIcon,Divider,List,Typography,Toolbar,AppBar,CssBaseline,Drawer} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import SearchIcon from '@material-ui/icons/Search';
import ArrowBack from '@material-ui/icons/ArrowBack';
import PersonOutlinedIcon from '@material-ui/icons/PersonOutlined';
import HomeIcon from '@material-ui/icons/Home';
import LocalShippingOutlinedIcon from '@material-ui/icons/LocalShippingOutlined';
import WorkOutlineOutlinedIcon from '@material-ui/icons/WorkOutlineOutlined';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Home from '../Pages/Acceuil';
import Personnes from '../Pages/Personne';
import Vehicules from '../Pages/Vehicule';
import Missions from '../Pages/Mission';
import MiniNav from './MiniNav';
import ReactDOM from 'react-dom';
import StorageIcon from '@material-ui/icons/Storage';
import LanguageIcon from '@material-ui/icons/Language';

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: '0 8px',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto',
    },
  },
  searchIcon: {
    width: theme.spacing(7),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: 120,
      '&:focus': {
        width: 240,
      },
    },
  },
}));


export default function NavBar() {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const [openMenuLogin, setopenMenuLogin] = React.useState(false);
  const [openMenuLanguage, setopenMenuLanguage] = React.useState(false);
  const [auth, setAuth] = React.useState(true);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchorLanguage, setanchorLanguage] = React.useState(null);
  const [anchorLogin, setanchorLogin] = React.useState(null);
  
  function handleDrawerOpen() {
    setOpen(true);
  }
  function handleDrawerClose() {
    setOpen(false);
  }
  function handleMenu(event) {
    setAnchorEl(event.currentTarget);
  }
  function handleClose() {
    setAnchorEl(null);
  }
  function handleMenuLanguage(event) {
    setanchorLanguage(event.currentTarget);
    setopenMenuLanguage(true);
  }
  function handleCloseLanguage() {
    setanchorLanguage(null);
    setopenMenuLanguage(false);
  }
  function handleMenuLogin(event) {
    setanchorLogin(event.currentTarget);
    setopenMenuLogin(true);
  }
  function handleCloseLogin() {
    setanchorLogin(null);
    setopenMenuLogin(false);
  }

  
  function Go_Mission(event) {
    ReactDOM.render(<Missions />, document.getElementById('corps'));
  }
  function Go_Acceuil(event) {
    ReactDOM.render(<Home />, document.getElementById('corps'));
  }
  function Go_Personne(event) {
    ReactDOM.render(<Personnes />, document.getElementById('corps'));
  }
  function Go_Vehicule(event) {
    ReactDOM.render(<Vehicules />, document.getElementById('corps'));
  }
  function Load_MiniNav(event) {
    ReactDOM.render(<MiniNav />, document.getElementById('root'));
  }
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, open && classes.hide)}
          >
            <MenuIcon />
          </IconButton>
          <Typography className={classes.title} variant="h6" noWrap>
            Gestion Parc Automobile
          </Typography>
          <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Search…"
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ 'aria-label': 'search' }}
            />
          </div>
          {auth && (
            <div>
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleMenuLogin}
                color="inherit"
              >
                <AccountCircle />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorLogin}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={openMenuLogin}
                onClose={handleCloseLogin}
              >
                <MenuItem onClick={handleCloseLogin}>Profile</MenuItem>
                <MenuItem onClick={handleCloseLogin}>Mon Compte</MenuItem>
                <MenuItem onClick={handleCloseLogin}>Se Deconnecter</MenuItem>
                <MenuItem onClick={handleCloseLogin}>Verrouiller</MenuItem>
              </Menu>
            </div>
          )}
          <div>
              <IconButton
                aria-label="account of current user"
                aria-controls="menus-appbar"
                aria-haspopup="true"
                onClick={Load_MiniNav}
                color="inherit"
              >
                <StorageIcon />
              </IconButton>
            </div>
            <div>
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-language"
                aria-haspopup="true"
                onClick={handleMenuLanguage}
                color="inherit"
              >
                <LanguageIcon />
              </IconButton>
              <Menu
                id="menu-language"
                anchorEl={anchorLanguage}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={openMenuLanguage}
                onClose={handleCloseLanguage}
              >
                <MenuItem onClick={handleCloseLanguage}>Français</MenuItem>
                <MenuItem onClick={handleCloseLanguage}>Anglais</MenuItem>
                <MenuItem onClick={handleCloseLanguage}>العربيــة</MenuItem>
              </Menu>
            </div>
        </Toolbar>
      </AppBar>
      <Drawer
        className={classes.drawer}
        variant="persistent"
        anchor="left"
        open={open}
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <div className={classes.drawerHeader}>
        <Typography className={classes.title} variant="h3" noWrap align="center">
        <LocalShippingOutlinedIcon fontSize='large'/>
          </Typography>
          <IconButton onClick={handleDrawerClose}>
          
            {theme.direction === 'ltr' ? <ArrowBack /> : <ChevronRightIcon />}
          </IconButton>
        </div>
        <Divider />
        <List>
          <Router>
            <ListItem button key='Acceuil' onClick={Go_Acceuil}>
              <ListItemIcon> <HomeIcon /></ListItemIcon>
              <ListItemText primary='Acceuil' />
            </ListItem>
            <ListItem button key='Vehicule' onClick={Go_Vehicule}>
              <ListItemIcon> <LocalShippingOutlinedIcon /></ListItemIcon>
              <ListItemText primary='Vehicule'/>
            </ListItem>
            <ListItem button key='Personne' onClick={Go_Personne}>
              <ListItemIcon> <PersonOutlinedIcon /></ListItemIcon>
              <ListItemText primary='Personne' />
            </ListItem>
            <ListItem button key='Mission' onClick={Go_Mission}>
              <ListItemIcon> <WorkOutlineOutlinedIcon /></ListItemIcon>
              <ListItemText primary='Mission' />
            </ListItem>
            <ListItem button key='Carburant' onClick={Go_Mission}>
              <ListItemIcon> <WorkOutlineOutlinedIcon /></ListItemIcon>
              <ListItemText primary='Carburant' />
            </ListItem>
          </Router>
        </List>
        <Divider />
        <List>
          {['Nothing'].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
      </Drawer>
      <main
        className={clsx(classes.content, {
          [classes.contentShift]: open,
        })} 
      >
        <div className={classes.drawerHeader} />
        <div id='corps'>

        </div>
        
      </main>
    </div>
  );
}