import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';

import CustomizedSnackbars from './Alert';
const useStyles = makeStyles(theme => ({
  form: {
    display: 'flex',
    flexDirection: 'column',
    margin: 'auto',
    width: 'fit-content',
  },
  formControl: {
    marginTop: theme.spacing(2),
    minWidth: 120,
  },
  formControlLabel: {
    marginTop: theme.spacing(1),
  },
}));

export default function MaxWidthDialog_Etat() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [designationFr, setdesignationFr] = React.useState('');
  const [designationAr, setdesignationAr] = React.useState('');
  const [fullWidth, setFullWidth] = React.useState(true);
  const [maxWidth, setMaxWidth] = React.useState('sm');             {/*"xs","sm","md","lg","xl"*/}


  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleChangedesignationAr = (event) => {
    setdesignationAr(event.target.value);
  };
  const handleChangedesignationFr = (event) => {
    setdesignationFr(event.target.value);
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    fetch('http://127.0.0.1:8080/Etats', {
      method: 'POST',
      body: JSON.stringify({
        'designationFr':designationFr,
        'designationAr':designationAr
      }),
      headers: {
        "Content-Type": "application/json"
      },
    }).then(function(response) {
        return(<CustomizedSnackbars/>);
    }, function(error) {
      alert(error.message); //=> String
    });
    handleClose();
  }
  return (
    <React.Fragment>
      <Button variant="outlined" color="primary" onClick={handleClickOpen}>
        Ajouter Etat
      </Button>
      <Dialog
        fullWidth={fullWidth}
        maxWidth={maxWidth}
        open={open}
        onClose={handleClose}
        aria-labelledby="max-width-dialog-title"
      >
        <DialogTitle id="max-width-dialog-title">        
                <h2>Ajouter un Etat</h2>
        </DialogTitle>
        <DialogContent>
        <form  className={classes.container}  >
          <TextField
            id="outlined-full-width"
            label="Designation Etat en Francais"
            style={{ margin: 8 }}
            placeholder="Designation Etat en Francais"
            helperText=""
            fullWidth
            margin="normal"
            variant="outlined"
            InputLabelProps={{
              shrink: true,
            }}
            onChange={handleChangedesignationFr}
          />
          <TextField
            id="outlined-full-width"
            label="Designation Etat en Arab"
            style={{ margin: 8 }}
            placeholder="التعيين باللغة العربية"
            helperText=""
            fullWidth
            dir= 'rtl'
            margin="normal"
            variant="outlined"
            className={classes.affected}
            InputLabelProps={{
              shrink: true,
            }}
            onChange={handleChangedesignationAr}
          />
       </form>
        </DialogContent>
        <DialogActions>
        <Button onClick={handleClose} color="secondary">
            Close
          </Button>
          <Button onClick={handleSubmit} color="primary">
            Valider
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}
