import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import Grid from '@material-ui/core/Grid';
const Etats = [
    {
      value: '5d585884a0fae13104e6e779',
      label: 'Neuf',
    },
  ];
  const Marques = [
    {
      value: '5d5860d6a0fae110b47a0792',
      label: 'Mercedes Benz',
    },
  ];
const Couleurs = [
    {
      value: '5d8de131f09cd5042486eaf7',
      label: 'Noire',
    },
    {
        value: 'Blue',
        label: 'Blue',
      },
  ];
  const Categories = [
    {
      value: '5d583e31a0fae132a88ee3b4',
      label: 'Bus',
    },
    {
        value: 'Voiture',
        label: 'Voiture',
    },
  ];
const Vidanges = [];
const ControlTechs = [];
const Assurances = [];
const Nombre_des_places = [
    {
      value: '1',
      label: 'Une Place',
    },
    {
      value: '2',
      label: 'Deux Places',
    },

    {
      value: '3',
      label: 'Trois Places',
    },

    {
      value: '4',
      label: 'Quatre Places',
    },
  ];

const useStyles = makeStyles(theme => ({
  container: {
   
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  dense: {
    marginTop: theme.spacing(2),
  },
  menu: {
    width: 500,
  },
}));

export default function OutlinedTextFields() {
  const classes = useStyles();
  const [values, setValues] = React.useState({
    name: 'Cat in the Hat',
    age: '',
    multiline: 'Controlled',
  });

  const handleChange = name => event => {
    setValues({ ...values, [name]: event.target.value });
  };

  return (

    <form  className={classes.container}  autoComplete="off">
        <h1></h1>
        
         <TextField
        id="outlined-full-width"
        label="Matricule"
        style={{ margin: 8 }}
        placeholder="Matricule"
        helperText=""
        fullWidth
        margin="normal"
        variant="outlined"
        InputLabelProps={{
          shrink: true,
        }}
      />
      <Grid container justify="center">
        <Grid item xs={12} sm={12} lg={4}> 
              <TextField
              id="outlined-number"
              label="Number"
              value={values.age}
              onChange={handleChange('age')}
              type="number"
              className={classes.textField}
              InputLabelProps={{
                shrink: true,
              }}
              margin="normal"
              variant="outlined"
            />
            <IconButton color="secondary" justify="center" className={classes.button} aria-label="">
              <AddCircleOutlineIcon />
            </IconButton>
            </Grid>
            <Grid item xs={12} sm={12} lg={4}> 

            <TextField
              id="outlined-select-currency"
              select
              label="Etat"
              className={classes.textField}
              value={values.Etats}
              onChange={handleChange('Etats')}
              SelectProps={{
                MenuProps: {
                  className: classes.menu,
                },
              }}
              helperText="Choisissez l'etat de Vehicule"
              margin="normal"
              variant="outlined"
            >
              {Etats.map(option => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
            <IconButton color="secondary" centered className={classes.button} aria-label="">
              <AddCircleOutlineIcon />
            </IconButton>
            </Grid>

            <Grid item xs={12} sm={12} lg={4}> 
            <TextField
              id="outlined-select-currency"
              select
              label="Categories"
              className={classes.textField}
              value={values.Categories}
              onChange={handleChange('Categories')}
              SelectProps={{
                MenuProps: {
                  className: classes.menu,
                },
              }}
              helperText="Choisissez l'etat de Vehicule"
              margin="normal"
              variant="outlined"
            >
              {Categories.map(option => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
            <IconButton color="secondary" justify="center" className={classes.button} aria-label="">
              <AddCircleOutlineIcon />
            </IconButton>
        </Grid>
      </Grid>
      
      <TextField
        id="outlined-full-width"
        label="Position au Garage"
        style={{ margin: 8 }}
        placeholder="Position au Garage"
        helperText=""
        fullWidth
        margin="normal"
        variant="outlined"
        InputLabelProps={{
          shrink: true,
        }}
      />
      
      <Grid container justify="center">
        <Grid item xs={12} sm={12} lg={4}> 
            <TextField
            id="outlined-select-currency"
            select
            label="Control Technique"
            className={classes.textField}
            value={values.ControlTechs}
            onChange={handleChange('ControlTechs')}
            SelectProps={{
              MenuProps: {
                className: classes.menu,
              },
            }}
            helperText="Choisissez l'etat de Vehicule"
            margin="normal"
            variant="outlined"
          >
            {ControlTechs.map(option => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
                <IconButton color="secondary" justify="center" className={classes.button} aria-label="">
                  <AddCircleOutlineIcon />
                </IconButton>
                </Grid>
                <Grid item xs={12} sm={12} lg={4}> 

                <TextField
            id="outlined-select-currency"
            select
            label="Assurance"
            className={classes.textField}
            value={values.Assurances}
            onChange={handleChange('Assurances')}
            SelectProps={{
              MenuProps: {
                className: classes.menu,
              },
            }}
            helperText="Choisissez l'etat de Vehicule"
            margin="normal"
            variant="outlined"
          >
            {Assurances.map(option => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
                <IconButton color="secondary" centered className={classes.button} aria-label="">
                  <AddCircleOutlineIcon />
                </IconButton>
                </Grid>

                <Grid item xs={12} sm={12} lg={4}> 
                <TextField
            id="outlined-select-currency"
            select
            
            label="Vidange"
            className={classes.textField}
            value={values.Vidanges}
            onChange={handleChange('Vidanges')}
            SelectProps={{
              MenuProps: {
                className: classes.menu,
              },
            }}
            helperText="Choisissez l'etat de Vehicule"
            margin="normal"
            variant="outlined"
          >
            {Vidanges.map(option => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
                <IconButton color="secondary" justify="center" className={classes.button} aria-label="">
                  <AddCircleOutlineIcon />
                </IconButton>
        </Grid>
      </Grid>


      <TextField
        id="outlined-select-currency"
        select
        label="Marque"
        className={classes.textField}
        value={values.Marques}
        onChange={handleChange('Marques')}
        SelectProps={{
          MenuProps: {
            className: classes.menu,
          },
        }}
        helperText="Choisissez l'etat de Vehicule"
        margin="normal"
        variant="outlined"
      >
        {Marques.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
      <TextField
        id="outlined-select-currency"
        select
        
        label="Couleur"
        className={classes.textField}
        value={values.Couleurs}
        onChange={handleChange('Couleurs')}
        SelectProps={{
          MenuProps: {
            className: classes.menu,
          },
        }}
        helperText="Choisissez l'etat de Vehicule"
        margin="normal"
        variant="outlined"
      >
        {Couleurs.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    </form>
  );
}
