import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme,fade } from '@material-ui/core/styles';
import {Menu,IconButton,InputBase,MenuItem,ListItem,ListItemText,ListItemIcon,Divider,List,Typography,Toolbar,AppBar,CssBaseline,Drawer} from '@material-ui/core';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import AccountCircle from '@material-ui/icons/AccountCircle';
import BuildIcon from '@material-ui/icons/Build';
import LocalShippingOutlinedIcon from '@material-ui/icons/LocalShippingOutlined';
import LocalShippingIcon from '@material-ui/icons/LocalShipping';
import WorkIcon from '@material-ui/icons/Work';
import Tooltip from '@material-ui/core/Tooltip';
import PersonIcon from '@material-ui/icons/Person';
import HomeIcon from '@material-ui/icons/Home';
import Missions from '../Pages/Mission';
import Home from '../Pages/Acceuil';
import Personnes from '../Pages/Personne';
import Configs from '../Pages/Config';
import Vehicules from '../Pages/Vehicule';
import Carburants from '../Pages/Carburant';
import ReactDOM from 'react-dom';
import NavBar from './NavBar';
import StorageIcon from '@material-ui/icons/Storage';
import LocalGasStationIcon from '@material-ui/icons/LocalGasStation';
import LanguageIcon from '@material-ui/icons/Language';
const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  menuButton: {
    marginRight: 36,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9) + 1,
    },
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto',
    },
  },
  searchIcon: {
    width: theme.spacing(7),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: 120,
      '&:focus': {
        width: 240,
      },
    },
  },
}));

export default function MiniDrawer() {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const [color,setColor]=React.useState('inherit');
  const [carburant,setCarburant]=React.useState('inherit');
  const [acceuil,setAcceuil]=React.useState('primary');
  const [personne,setPersonne]=React.useState('inherit');
  const [config,setConfig]=React.useState('inherit');
  const [vehicule,setVehicule]=React.useState('inherit');
  const [titleBar,settitleBar]=React.useState('');
  
 
  const [openMenuLogin, setopenMenuLogin] = React.useState(false);
  const [openMenuLanguage, setopenMenuLanguage] = React.useState(false);
  function handleDrawerOpen() {
    setOpen(true);
  }

  function handleDrawerClose() {
    setOpen(false);
  }
  
  const [auth, setAuth] = React.useState(true);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchorLanguage, setanchorLanguage] = React.useState(null);
  const [anchorLogin, setanchorLogin] = React.useState(null);
  
  function handleMenuLogin(event) {
    setanchorLogin(event.currentTarget);
    setopenMenuLogin(true);
  }

  function handleCloseLogin() {
    setanchorLogin(null);
    setopenMenuLogin(false);
  }
  function Load_MiniNav(event) {
    ReactDOM.render(<NavBar />, document.getElementById('root'));
    
  }
  function Go_Mission(event) {
    ReactDOM.render(<Missions />, document.getElementById('corps'));
    settitleBar('Missions');
    setColor('primary');
    setConfig('inherit');
    setAcceuil('inherit');
    setPersonne('inherit');
    setVehicule('inherit');
    setCarburant('inherit');
    
  }
  function Go_Config(event) {
    ReactDOM.render(<Configs />, document.getElementById('corps'));
    settitleBar('Configurations');
    setConfig('primary');
    setColor('inherit');
    setAcceuil('inherit');
    setPersonne('inherit');
    setVehicule('inherit');
    setCarburant('inherit');
  }
  function Go_Acceuil(event) {
    ReactDOM.render(<Home />, document.getElementById('corps'));
    settitleBar('Acceuil');
    setAcceuil('primary');
    setColor('inherit');
    setConfig('inherit');
    setPersonne('inherit');
    setVehicule('inherit');
    setCarburant('inherit');
  }
  function Go_Personne(event) {
    ReactDOM.render(<Personnes />, document.getElementById('corps'));
    settitleBar('Personnes');
    setPersonne('primary');
    setAcceuil('inherit');
    setColor('inherit');
    setConfig('inherit');
    setVehicule('inherit');
    setCarburant('inherit');
  }
  function Go_Vehicule(event) {
    ReactDOM.render(<Vehicules />, document.getElementById('corps'));
    settitleBar('Vehicules');
    setVehicule('primary');
    setAcceuil('inherit');
    setColor('inherit');
    setConfig('inherit');
    setPersonne('inherit');
    setCarburant('inherit');
  }
  function Go_Carburant(event) {
    ReactDOM.render(<Carburants />, document.getElementById('corps'));
    settitleBar('Carburants');
    setCarburant('primary');
    setAcceuil('inherit');
    setColor('inherit');
    setConfig('inherit');
    setPersonne('inherit');
    setVehicule('inherit');
  }
  function handleMenuLanguage(event) {
    setanchorLanguage(event.currentTarget);
    setopenMenuLanguage(true);
  }

  function handleCloseLanguage() {
    setanchorLanguage(null);
    setopenMenuLanguage(false);
  }
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open,
            })}
          >
            <MenuIcon />
          </IconButton>
          <Typography className={classes.title} variant="h6" noWrap>
            Gestion Parc Automobile
          </Typography>
          <Typography className={classes.title} variant="h6" wrap>
            {titleBar}
          </Typography>
          <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Rechercher..."
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ 'aria-label': 'search' }}
            />
          </div>
          {auth && (
            <div>
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleMenuLogin}
                color="inherit"
              >
                <AccountCircle />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorLogin}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={openMenuLogin}
                onClose={handleCloseLogin}
              >
                <MenuItem onClick={handleCloseLogin}>Profile</MenuItem>
                <MenuItem onClick={handleCloseLogin}>Mon Compte</MenuItem>
                <MenuItem onClick={handleCloseLogin}>Se Deconnecter</MenuItem>
                <MenuItem onClick={handleCloseLogin}>Verrouiller</MenuItem>
              </Menu>
            </div>
          )}
          <div>
              <IconButton
                aria-label="account of current user"
                aria-controls="menus-appbar"
                aria-haspopup="true"
                onClick={Load_MiniNav}
                color="inherit"
              >
                <StorageIcon />
              </IconButton>
            </div>
            <div>
            <IconButton
                aria-label="account of current user"
                aria-controls="menu-language"
                aria-haspopup="true"
                onClick={handleMenuLanguage}
                color="inherit"
              >
                <LanguageIcon />
              </IconButton>
              <Menu
                id="menu-language"
                anchorEl={anchorLanguage}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={openMenuLanguage}
                onClose={handleCloseLanguage}
              >
                <MenuItem onClick={handleCloseLanguage}>Français</MenuItem>
                <MenuItem onClick={handleCloseLanguage}>Anglais</MenuItem>
                <MenuItem onClick={handleCloseLanguage}>العربيــة</MenuItem>
              </Menu>
            </div>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
        open={open}
      >
        <div className={classes.toolbar}>
        <Typography className={classes.title} variant="h3" noWrap align="center">
        <LocalShippingOutlinedIcon fontSize='large'/>
          </Typography>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
          </IconButton>
        </div>
        <Divider />
        <List>
            <Tooltip title="Acceuil" placement="right">
              <ListItem button key='Acceuil' onClick={Go_Acceuil} >
                <ListItemIcon> <HomeIcon fontSize='large' color={acceuil}/></ListItemIcon>
                <ListItemText primary='Acceuil' />
              </ListItem>
            </Tooltip>
            <Tooltip title="Vehicule" placement="right">
              <ListItem button backgroundColor='primary' key='Vehicule' onClick={Go_Vehicule}>
                <ListItemIcon> <LocalShippingIcon fontSize='large' color={vehicule}/></ListItemIcon>
                <ListItemText primary='Vehicule'/>
              </ListItem>
            </Tooltip>
            <Tooltip title="Personne" placement="right">
              <ListItem button key='Personne' onClick={Go_Personne}>
                <ListItemIcon> <PersonIcon fontSize='large' color={personne}/></ListItemIcon>
                <ListItemText primary='Personne' />
              </ListItem>
            </Tooltip>
            <Tooltip title="Mission" placement="right">
              <ListItem button key='Mission' onClick={Go_Mission}>
                <ListItemIcon> <WorkIcon id='missionIcon' color={color} fontSize='large'/></ListItemIcon>
                <ListItemText primary='Mission' />
              </ListItem>
            </Tooltip>
            <Tooltip title="Carburant" placement="right">
              <ListItem button key='Carburant' onClick={Go_Carburant}>
                <ListItemIcon> <LocalGasStationIcon color={carburant} fontSize='large'/></ListItemIcon>
                <ListItemText primary='Carburant' />
              </ListItem>
            </Tooltip>
        </List>
        <Divider />
        <Divider />
        <List>
        <ListItem button key='Config' onClick={Go_Config}>
              <ListItemIcon> <BuildIcon fontSize='large' color={config}/></ListItemIcon>
              <ListItemText primary='Config' />
            </ListItem>
        </List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <div id='corps'> 
          <Home/>
        </div>
      
      </main>
    </div>
  );
}
